.modal {
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
    border-radius: 6px;
    background-color: white;
    padding: 1rem;
    text-align: center;
    width: 30rem;
    z-index: 10;
    position: fixed;
    top: 20vh;
    left: calc(50% - 15rem);
}

.btn--alt {
    background-color: transparent;
    color: #800040;
}
  
.btn--alt:hover {
    background-color: #f8dae9;
}

.btn {
    font: inherit;
    padding: 0.5rem 1.5rem;
    cursor: pointer;
    border-radius: 4px;
    background-color: #3c2846;
    color: white;
    border: 1px solid #3c2846;
    margin: 0 1rem;
}
  
.btn:hover {
    background-color: #3c2846;
    border-color: #3c2846;
}

.modal p {
    color: #000;
}