:root {
  --main-color: #3c2846;
  --light-pink: #ca90e0;
  --dark-purple: #1c1320;
}

* {
  color: #fff;
  font-family: "Roboto Mono", monospace;
}

html,
body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: auto;
}

.card {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 400px;
  height: 500px;
  background: #ca90e0;
  background: var(--light-pink);
  transform-style: preserve-3d;
  margin: 0 2%;
  cursor: pointer;
  transform: perspective(2000px);
  transition: 1s;
  font-weight: 300;
}

.card:hover {
  transform: perspective(2000px) translateX(50%);
}

.card .cover {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  transform-style: preserve-3d;
  transition: 1s ease-in-out;
  transform-origin: left;
}

.card:hover {
  transform: rotateY(-180deg);
}

.card .details {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  text-align: center;
  z-index: 1;
  transform: rotateY(180deg);
}

.card::before {
  content: "";
  position: absolute;
  top: -4px;
  left: -4px;
  right: -4px;
  bottom: -4px;
  transform: skewX(2deg) skewY(4deg);
  background: linear-gradient(315deg, #00ccff, #1c1320, #d400d4);
  background: linear-gradient(315deg, #00ccff, var(--dark-purple), #d400d4);
}

.card::after {
  content: "";
  position: absolute;
  top: -4px;
  left: -4px;
  right: -4px;
  bottom: -4px;
  transform: skewX(2deg) skewY(4deg);
  background: linear-gradient(315deg, #00ccff, #1c1320, #d400d4);
  background: linear-gradient(315deg, #00ccff, var(--dark-purple), #d400d4);
  filter: blur(50px);
}

.card span {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: #1c1320;
  background: var(--dark-purple);
  z-index: 10;
  font-size: 16px;
}

.card ion-icon {
  font-size: 40px;
  margin-right: 10px;
  cursor: pointer;
}

.question {
  text-align: center;
  padding: 5%;
  color: #d400d4;
  font-weight: 300;
}

.answer {
  display: block; /* or inline-block */
  text-overflow: ellipsis;
  word-wrap: break-word;
  overflow: hidden;
  line-height: 1.5em;
}

.listed-container {
  position: absolute;
  bottom: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding-left: 20px;
}

.listed-link {
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.details-button {
  display: inline-block;
  position: absolute;
  top: 85%;
  right: 15%;
  padding: 6px 30px;
  background: #d400d4;
  color: #fff;
  letter-spacing: 1px;
  text-decoration: none;
  border-radius: 15px;
  font-weight: 500;
}

